import * as Utils from "./metadata/utils";
var Reflect = require('reflect-metadata/Reflect.js');

export function field(target: Object, propertyKey: string) {
    
    console.log('field - propertyKey: ', propertyKey, ', target:', target);

    Utils.addMetaData(<Utils.IMetaTarget>target, "field", Utils.DecoratorType.PROPERTY, null, propertyKey);
}
